import request from '@/utils/request'

export default {
  // 根据手机号发送验证码
  sendCode(phone) {
    return request({
      url: `/edusms/sms/send/${phone}`,
      method: 'post'
    })
  },
  // 用户注册
  register(registerVo) {
    return request({
      url: `/ucenter/member/register`,
      method: 'post',
      data: registerVo
    })
  }
}